.grid {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
}

.user {
	background: var(--alt-background);
	height: 7rem;
	width: 45%rem;
	border-radius: 1.5rem;
	border: 0.25rem solid transparent;
	color: var(--color);
	margin: 1rem;
	transition: border .5s ease;
}
.user:hover {
	color: var(--color);
	border: 0.25rem solid var(--main-color);
}

.flex {
	display: flex;
	justify-content: space-evenly;
}
.icon {
	height: 1.5rem;
	width: 1.5rem;
	margin: 0 0.2rem;
}