.main {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
}

.navigator {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background: var(--alt-background);
	height: calc(20rem - 1rem * 2);
	width: calc(20rem - 1rem * 2);
	padding: 1rem;
	border-radius: 1.5rem;
	border: 0.25rem solid transparent;
	color: var(--color);
	margin: 1rem;
	transition: border .5s ease;
	overflow: hidden;
}
.navigator:hover {
	color: var(--color);
	border: 0.25rem solid var(--main-color);
}
.navigator img {
	height: calc(100% - 5rem);
}
.navigator h1 {
	height: 5rem;
	margin: 0;
	padding: 0;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	text-overflow: ellipsis;
	text-align: center;
}
.flex {
	display: flex;
	justify-content: space-evenly;
}
.icon {
	height: 1.5rem;
	width: 1.5rem;
	margin: 0 0.2rem;
}