.users {
	width: calc(100% - 2rem * 2 );
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding: .5rem 2rem;
}
.user {
	background: var(--alt-background) no-repeat center/cover;
	height: 25rem;
	width: 25rem;
	position: relative;
	border-radius: .75rem;
	margin: 1.5rem;
	padding: .25rem;
	display: flex;
	flex-direction: column;
}
.user *::selection {
	background: #0074ffcc;
}
.banner {
	width: 100%;
	height: 9rem;
	border-radius: .75rem .75rem 0 0;
}
.avatar {
	border-radius: 100%;
	height: 5.5rem;
	width: 5.5rem;
	position: absolute;
	top: calc(9rem - 5.5rem / 2 - 1rem);
	left: 1rem;
	border: .25rem solid #444;
	background: #333;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 2rem;
	margin: 0;
	padding: 0;
}
.copy_token {
	position: absolute;
	top: calc(9rem + 2rem / 2);
	right: calc(1rem);
	height: 2rem;
	width: 2rem;
	border-radius: 100%;
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #111;
}
.copy_token span {
	font-size: 1rem;
}
.metadata {
	padding: 1.5rem .5rem 0 .5rem;
	height: 55%;
	width: calc(100% - .5rem * 2);
	background: transparent no-repeat center/cover;
}
.username {
	margin: 0;
}
.flex {
	display: flex;
	align-items: center;
}
.name {
	margin: 0;
}
.badges {
	background: #111;
	border-radius: .75rem;
	display: flex;
	align-items: center;
	height: 1.25rem;
	min-width: min-content;
	max-width: 10rem;
}
.badge {
	height: 80%;
	margin: 0 .25rem;
	padding: 0;
	position: relative;
}
.badge img {
	height: 100%;
}
.bio > p {
	background: #11111170;
	border-radius: .25rem;
	height: calc(9rem - .5rem * 2);
	width: calc(100% - .5rem * 2);
	padding: .5;
	margin: 1rem 0;
	overflow: hidden auto;
}
.bio code {
	background: #222;
	padding: .1rem;
}