.section {
	width: 100%;
}
.section:nth-child(2n) {
	background: #00000050;
}
.section h1 {
	padding: .5rem 1rem;
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: calc(100% - 1rem * 2);
}

.cookies {
	display: flex;
	justify-content: center;
	flex-direction: column;
	width: calc(100% - 1rem * 2);
	padding: 1rem;
}

.cookie {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.cookie h4 {
	padding: 0;
	width: 15rem;
	overflow: auto hidden;
}
.censured_token {
	width: calc(100% - 15rem - .5rem * 2);
	overflow: hidden;
	text-overflow: ellipsis;
	background: var(--alt-background);
	padding: .25rem .5rem;
	border-radius: .2rem;
	margin: 0 1rem;
}
.copy {
	height: calc(2.5rem - 1rem * 2);
	width: calc(2.5rem - 1rem * 2);
	border-radius: 100%;
	padding: 1rem;
	display: flex;
	justify-content: center;
	align-items: center;
}