#page {
	height: 100%;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
#container {
	height: 50%;
	width: 60%;
	background: var(--alt-background);
	display: flex;
	justify-content: space-between;
	align-items: center;
}
#container img {
	height: 100%;
	max-width: 45%;
	object-fit: cover;
}
#form {
	display: flex;
	flex-direction: column;
	padding: 1rem;
}
#form h1 {
	position: relative;
}
#form h1::after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	height: .25rem;
	margin: 0 .25rem;
	background: var(--main-color);
	animation: sizer 1.5s infinite ease;
}
.flex {
	display: flex;
	justify-content: center;
	align-items: center;
}
#form input {
	background: var(--alt-2-background);
	height: 1.5rem;
}
.flex input {
	width: calc(100% - 2.5rem);
}
.flex button {
	width: 2.5rem;
	margin: 0;
	padding: .5rem;
	background: none;
}
#login {
	background: var(--main-color);
}

@keyframes sizer {
	0% {
		width: 10%;
	}
	50% {
		width: 30%;
	}
	100% {
		width: 10%;
	}
}