#header {
	height: 5rem;
	width: 100%;
	background: var(--alt-background);
	border-bottom: .25rem solid var(--main-color);
	display: flex;
	justify-content: space-between;
	align-items: center;
}
#logo {
	display: flex;
	justify-content: center;
	align-items: center;
	height: calc(100% - 1rem * 2);
	width: 2rem;
	padding: 1rem;
}
#logo img {
	height: 100%;
}
#disconnect, #getback {
	background: var(--background);
	border-radius: 100%;
	width: 2.5rem;
	height: 2.5rem;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: .25rem;
}